import React, { useEffect, useRef, useState } from "react";
import { StyledAboutUs, FirstOfferSec } from "./aboutUs.styled";

import fetchByCategory from "../gallery/helpers/fetchByCategory";
import urlFor from "../gallery/helpers/urlFor";

const AboutUs = () => {
  const [images, setImages] = useState(null);
  const [captions, setCaptions] = useState(null);

  useEffect(() => {
    fetchByCategory("aboutUs", setImages, setCaptions);
  }, []);

  return (
    <StyledAboutUs>
      <FirstOfferSec
        className="firstOfferSec"
        img={images ? urlFor(images[0]).url() : null}
      ></FirstOfferSec>
      <div className="secondOfferSec">
        <h2>
          {" "}
          <a name="aboutUs">Twj architekt</a>
        </h2>

        <p>
          Jestem absolwentem Politechniki Gdaskiej. Projektowaniem zajmuj si
          od ponad 25 lat, wasn dziaalno prowadz od 4 lat. W tym czasie
          zaprojektowaem niezliczon ilo obiektw budowlanych. S to midzy
          innymi domy mieszkalne jednorodzinne i wielorodzinne, sklepy,
          restauracje, hotele, budynki przemysowe i rolnicze. Na pocztku mojej
          pracy zawodowej projektowaem tylko i wycznie w technologii 2D.
          Obecnie po nabraniu dowiadczenia i udoskonaleniu swoich umiejtnoci,
          projektuj gwnie w technologii 3D. Dziki temu klientowi atwiej
          jest podj decyzj co do zaproponowanych rozwiza.
        </p>
      </div>
    </StyledAboutUs>
  );
};

export default AboutUs;
