import React from "react";
import { StyledOffer } from "./offer.styled";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faListCheck } from "@fortawesome/free-solid-svg-icons";

const Offer = () => {
  return (
    <StyledOffer name="offer">
      <div id="firstOfferSec">
        <ul>
          <li>
            <a href="/" name="offer">
              {"  "}
            </a>
          </li>
          <li>
            <h2>Nasza Oferta</h2>
          </li>
          <li>
            - projekty budynkw mieszkalnych jednorodzinnych, wielorodzinnych,
            usugowych, uytecznoci publicznej, produkcyjnych, itp.
          </li>
          <li>
            {" "}
            - adaptacje projektw typowych (katalogowych) do warunkw
            miejscowych, wraz z ewentualnymi zmianami w projekcie
          </li>
          <li>
            {" "}
            - projekty rozbudowy i przebudowy budynkw -projekty rozbirki
            budynkw
          </li>
          <li>- inwentaryzacje budynkw</li>
          <li>- projekty przyczy </li>
          <li>- uzyskiwanie zmiany sposobu uytkowania budynkw </li>
          <li>- wizualizacj przestrzenne budynkw </li>
        </ul>
      </div>
      <div id="secondOfferSec">
        <FontAwesomeIcon
          icon={faListCheck}
          color="white"
          style={{ fontSize: "20rem" }}
        />
      </div>
    </StyledOffer>
  );
};

export default Offer;
